<script>
  export let changeChildCount1;
  export let changeFlexDirection1;
  export let changeJustifyContent1;
  export let changeAlignItems1;

  export let flexDirection1;
  export let justifyContent1;
  export let alignItems1;

  export let withParent2;
  export let changeWithParent2;

  let printOutput1 = "";
  $: {
    printOutput1 = "";
    if (flexDirection1.startsWith("column")) {
      printOutput1 += "Column(";
    }
    if (flexDirection1 === "column-reverse") {
      printOutput1 += "\n verticalDirection: VerticalDirection.up,";
    }
    if (flexDirection1.startsWith("row")) {
      printOutput1 += "Row(";
    }
    if (flexDirection1 === "row-reverse") {
      printOutput1 += "\n textDirection: TextDirection.rtl,";
    }

    if (justifyContent1 === "flex-end") {
      printOutput1 += "\n mainAxisAlignment: MainAxisAlignment.end,";
    }
    if (justifyContent1 === "center") {
      printOutput1 += "\n mainAxisAlignment: MainAxisAlignment.center,";
    }
    if (justifyContent1 === "space-around") {
      printOutput1 += "\n mainAxisAlignment: MainAxisAlignment.spaceAround,";
    }
    if (justifyContent1 === "space-between") {
      printOutput1 += "\n mainAxisAlignment: MainAxisAlignment.spaceBetween,";
    }

    if (alignItems1 === "stretch") {
      printOutput1 += "\n crossAxisAlignment: CrossAxisAlignment.stretch,";
    }
    if (alignItems1 === "baseline") {
      printOutput1 += "\n crossAxisAlignment: CrossAxisAlignment.baseline,";
    }
    if (alignItems1 === "flex-start") {
      printOutput1 += "\n crossAxisAlignment: CrossAxisAlignment.start,";
    }
    if (alignItems1 === "flex-end") {
      printOutput1 += "\n crossAxisAlignment: CrossAxisAlignment.end,";
    }
    printOutput1 += "\n children: <Widget>[],";
    printOutput1 += "\n)";
  }
</script>

<div>

  <h2>Parent 1</h2>

  <div class="child-count-container">
    <div class="control-heading child-count-info">Change child count</div>
    <input
      id="ChildCount"
      class="child-count-slider"
      type="range"
      min="1"
      max="8"
      on:change={e => {
        changeChildCount1(e.target.value);
      }}
      value="3" />
  </div>

  <div class="flex-attribute-control flex-direction">
    <div class="control-heading">flex-direction</div>
    <div class="control-explanation">
      row => textDirection: | column => verticalDirection:
    </div>
    <div class="control-group">
      <label class="radio-container">
        <input
          on:click={e => {
            changeFlexDirection1(e.target.value);
          }}
          type="radio"
          name="flex-direction"
          value="row" />
        <span class="checkmark" />
        row
      </label>
      <label class="radio-container">
        <input
          on:click={e => {
            changeFlexDirection1(e.target.value);
          }}
          type="radio"
          name="flex-direction"
          value="row-reverse" />
        <span class="checkmark" />
        row-reverse
      </label>
      <label class="radio-container">
        <input
          on:click={e => {
            changeFlexDirection1(e.target.value);
          }}
          type="radio"
          name="flex-direction"
          value="column"
          checked="checked" />
        <span class="checkmark" />
        column
      </label>
      <label class="radio-container">
        <input
          on:click={e => {
            changeFlexDirection1(e.target.value);
          }}
          type="radio"
          name="flex-direction"
          value="column-reverse" />
        <span class="checkmark" />
        column-reverse
      </label>
    </div>
  </div>

  <div class="flex-attribute-control justify-content">
    <div class="control-heading">justify-content</div>
    <div class="control-explanation">= mainAxisAlignment:</div>
    <div class="control-group">
      <label class="radio-container">
        <input
          on:click={e => {
            changeJustifyContent1(e.target.value);
          }}
          type="radio"
          name="justify-content"
          value="flex-start"
          checked="checked" />
        <span class="checkmark" />
        flex-start (default)
      </label>
      <label class="radio-container">
        <input
          on:click={e => {
            changeJustifyContent1(e.target.value);
          }}
          type="radio"
          name="justify-content"
          value="flex-end" />
        <span class="checkmark" />
        flex-end
      </label>
      <label class="radio-container">
        <input
          on:click={e => {
            changeJustifyContent1(e.target.value);
          }}
          type="radio"
          name="justify-content"
          value="center" />
        <span class="checkmark" />
        center
      </label>
      <label class="radio-container">
        <input
          on:click={e => {
            changeJustifyContent1(e.target.value);
          }}
          type="radio"
          name="justify-content"
          value="space-around" />
        <span class="checkmark" />
        space-around
      </label>
      <label class="radio-container">
        <input
          on:click={e => {
            changeJustifyContent1(e.target.value);
          }}
          type="radio"
          name="justify-content"
          value="space-between" />
        <span class="checkmark" />
        space-between
      </label>
    </div>
  </div>

  <div class="flex-attribute-control align-items">
    <div class="control-heading">align-items</div>
    <div class="control-explanation">= crossAxisAlignment:</div>
    <div class="control-group">
      <label class="radio-container">
        <input
          on:click={e => {
            changeAlignItems1(e.target.value);
          }}
          type="radio"
          name="align-items"
          value="stretch" />
        <span class="checkmark" />
        stretch
      </label>
      <label class="radio-container">
        <input
          on:click={e => {
            changeAlignItems1(e.target.value);
          }}
          type="radio"
          name="align-items"
          value="baseline" />
        <span class="checkmark" />
        baseline
      </label>
      <label class="radio-container">
        <input
          on:click={e => {
            changeAlignItems1(e.target.value);
          }}
          type="radio"
          name="align-items"
          value="center"
          checked="checked" />
        <span class="checkmark" />
        center (default)
      </label>
      <label class="radio-container">
        <input
          on:click={e => {
            changeAlignItems1(e.target.value);
          }}
          type="radio"
          name="align-items"
          value="flex-start" />
        <span class="checkmark" />
        flex-start
      </label>
      <label class="radio-container">
        <input
          on:click={e => {
            changeAlignItems1(e.target.value);
          }}
          type="radio"
          name="align-items"
          value="flex-end" />
        <span class="checkmark" />
        flex-end
      </label>
    </div>
  </div>

  <div class="flex-attribute-control parent2">
    <div class="control-explanation">With parent 2</div>
    <div class="control-group">
      <label class="radio-container">
        <input
          on:click={e => {
            if (withParent2) {
              changeWithParent2(false);
            } else {
              changeWithParent2(true);
            }
          }}
          type="checkbox"
          name="parent2" />
        <span class="checkmark" />
        With Parent 2
      </label>
    </div>
  </div>

  <div class="preview-container">
    <div class="control-heading">FLUTTER WIDGET OUTPUT</div>

    <pre class="preview">{printOutput1}</pre>

  </div>

</div>
