<script>
  export let changeChildCount2;
  export let changeFlexDirection2;
  export let changeJustifyContent2;
  export let changeAlignItems2;

  export let flexDirection2;
  export let justifyContent2;
  export let alignItems2;

  let printOutput2 = "";
  $: {
    printOutput2 = "";
    if (flexDirection2.startsWith("column")) {
      printOutput2 += "Column(";
    }
    if (flexDirection2 === "column-reverse") {
      printOutput2 += "\n verticalDirection: VerticalDirection.up,";
    }
    if (flexDirection2.startsWith("row")) {
      printOutput2 += "Row(";
    }
    if (flexDirection2 === "row-reverse") {
      printOutput2 += "\n textDirection: TextDirection.rtl,";
    }

    if (justifyContent2 === "flex-end") {
      printOutput2 += "\n mainAxisAlignment: MainAxisAlignment.end,";
    }
    if (justifyContent2 === "center") {
      printOutput2 += "\n mainAxisAlignment: MainAxisAlignment.center,";
    }
    if (justifyContent2 === "space-around") {
      printOutput2 += "\n mainAxisAlignment: MainAxisAlignment.spaceAround,";
    }
    if (justifyContent2 === "space-between") {
      printOutput2 += "\n mainAxisAlignment: MainAxisAlignment.spaceBetween,";
    }
    if (alignItems2 === "stretch") {
      printOutput2 += "\n crossAxisAlignment: CrossAxisAlignment.stretch,";
    }
    if (alignItems2 === "baseline") {
      printOutput2 += "\n crossAxisAlignment: CrossAxisAlignment.baseline,";
    }
    if (alignItems2 === "flex-start") {
      printOutput2 += "\n crossAxisAlignment: CrossAxisAlignment.start,";
    }
    if (alignItems2 === "flex-end") {
      printOutput2 += "\n crossAxisAlignment: CrossAxisAlignment.end,";
    }
    printOutput2 += "\n children: <Widget>[],";
    printOutput2 += "\n)";
  }
</script>

<div>

  <h2>Parent 2</h2>

  <div class="child-count-container">
    <div class="control-heading child-count-info">Change child count</div>
    <input
      id="ChildCount"
      class="child-count-slider"
      type="range"
      min="2"
      max="8"
      on:change={e => {
        changeChildCount2(e.target.value);
      }}
      value="2" />
  </div>

  <div class="flex-attribute-control flex-direction">
    <div class="control-heading">flex-direction</div>
    <div class="control-explanation">
      row => textDirection: | column => verticalDirection:
    </div>
    <div class="control-group">
      <label class="radio-container">
        <input
          on:click={e => {
            changeFlexDirection2(e.target.value);
          }}
          type="radio"
          name="flex-direction2"
          value="row"
          checked="checked" />
        <span class="checkmark" />
        row
      </label>
      <label class="radio-container">
        <input
          on:click={e => {
            changeFlexDirection2(e.target.value);
          }}
          type="radio"
          name="flex-direction2"
          value="row-reverse" />
        <span class="checkmark" />
        row-reverse
      </label>
      <label class="radio-container">
        <input
          on:click={e => {
            changeFlexDirection2(e.target.value);
          }}
          type="radio"
          name="flex-direction2"
          value="column" />
        <span class="checkmark" />
        column
      </label>
      <label class="radio-container">
        <input
          on:click={e => {
            changeFlexDirection2(e.target.value);
          }}
          type="radio"
          name="flex-direction2"
          value="column-reverse" />
        <span class="checkmark" />
        column-reverse
      </label>
    </div>
  </div>

  <div class="flex-attribute-control justify-content">
    <div class="control-heading">justify-content</div>
    <div class="control-explanation">= mainAxisAlignment:</div>
    <div class="control-group">
      <label class="radio-container">
        <input
          on:click={e => {
            changeJustifyContent2(e.target.value);
          }}
          type="radio"
          name="justify-content2"
          value="flex-start"
          checked="checked" />
        <span class="checkmark" />
        flex-start (default)
      </label>
      <label class="radio-container">
        <input
          on:click={e => {
            changeJustifyContent2(e.target.value);
          }}
          type="radio"
          name="justify-content2"
          value="flex-end" />
        <span class="checkmark" />
        flex-end
      </label>
      <label class="radio-container">
        <input
          on:click={e => {
            changeJustifyContent2(e.target.value);
          }}
          type="radio"
          name="justify-content2"
          value="center" />
        <span class="checkmark" />
        center
      </label>
      <label class="radio-container">
        <input
          on:click={e => {
            changeJustifyContent2(e.target.value);
          }}
          type="radio"
          name="justify-content2"
          value="space-around" />
        <span class="checkmark" />
        space-around
      </label>
      <label class="radio-container">
        <input
          on:click={e => {
            changeJustifyContent2(e.target.value);
          }}
          type="radio"
          name="justify-content2"
          value="space-between" />
        <span class="checkmark" />
        space-between
      </label>
    </div>
  </div>

  <div class="flex-attribute-control align-items">
    <div class="control-heading">align-items</div>
    <div class="control-explanation">= crossAxisAlignment:</div>
    <div class="control-group">
      <label class="radio-container">
        <input
          on:click={e => {
            changeAlignItems2(e.target.value);
          }}
          type="radio"
          name="align-items2"
          value="stretch" />
        <span class="checkmark" />
        stretch
      </label>
      <label class="radio-container">
        <input
          on:click={e => {
            changeAlignItems2(e.target.value);
          }}
          type="radio"
          name="align-items2"
          value="baseline" />
        <span class="checkmark" />
        baseline
      </label>
      <label class="radio-container">
        <input
          on:click={e => {
            changeAlignItems2(e.target.value);
          }}
          type="radio"
          name="align-items2"
          value="center"
          checked="checked" />
        <span class="checkmark" />
        center (default)
      </label>
      <label class="radio-container">
        <input
          on:click={e => {
            changeAlignItems2(e.target.value);
          }}
          type="radio"
          name="align-items2"
          value="flex-start" />
        <span class="checkmark" />
        flex-start
      </label>
      <label class="radio-container">
        <input
          on:click={e => {
            changeAlignItems2(e.target.value);
          }}
          type="radio"
          name="align-items2"
          value="flex-end" />
        <span class="checkmark" />
        flex-end
      </label>
    </div>
  </div>

  <div class="preview-container">
    <div class="control-heading">FLUTTER WIDGET OUTPUT</div>

    <pre class="preview">{printOutput2}</pre>

  </div>

</div>
